*{
  background-color: #1a1a1c;
  color: #fff;
}
.coin-container{
  display: flex;
  justify-content: center;
}

.coin-row{
 display: flex;
 flex-direction: row;
 justify-items:start;
 align-items: center;
 height: 80px;
 border-bottom:1px solid black ;
 width: 900px
 ;
}

.coin{
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}
.coin h1{
  font-size: 16px;
  width: 150px;
}
.coin img{
  width:30px;
  margin-right: 10px;
}
.coin-symbol{
  text-transform: uppercase;
}
.coin-data{
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}
.coin-price{
  width: 155px;
}
.coin.percent{
  width: 100%;
}
.red{
  color: #f00606;
}
.green{
 color:  #11d811
}
.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}
.coin-search{
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.coin-text{
  margin-bottom:32px;
  text-align: center;
}
.coin-input{
  padding-left:16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg,#ac32e4 0%,#7918f2 48%,#4801ff 100%);
  color:#e2e2e2;
}
.coin-input::placeholder{
  color:#e2e2e2
}
